<template>
  <div class="notice_box">
    <div class="user_list">
      <p>报名人数</p>
      <ul class="clear">
        <li v-for="item in user">
          <img :src=$utils.api+item.face>
          <p>{{item.nickname}}</p>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
    export default {
      name: "user",
      props: {
        user: {
          type: Object,
          default: "",
        }
      },

    }
</script>

<style scoped>
  .notice_box{
    padding: 10px;
  }
  .tiem_box span:last-child{
    float: right;
  }

  .footer p:last-child{
    width: 50%;
    float: left;
    background-color: #E80414;
    font-size: .14rem;
    color: white;
  }
  .footer p:first-child{
    background-color: white;
    width: 50%;
    float: left;
    font-size: .14rem;
    color: #E80414;
  }
  .user_list{
    margin-bottom: .5rem;
  }
  .user_list>p{
    font-size: .14rem;
    color: #999999;
    padding: 10px 0;
    border-bottom: 1px solid  #EEEEEE;;
    margin-bottom: 10px;
  }
  .user_list ul li{
    width: .7rem;
    text-align: center;
    float: left;
    margin-bottom: 5px;
  }
  .user_list ul li img{
    width: .5rem;
    height: .5rem;
    border-radius: 50%;
    object-fit: cover;
  }
  .user_list ul li p{
    margin: 5px 0;
  }
</style>



