import Vue from 'vue';
import {Toast} from 'mint-ui';
import axios from 'axios';
// import router from '../../router';
import ajaxs from '../../api/share';
import qs from 'qs';

const utils = {
  api: 'http://kouzhiwenapi.zpftech.com',
}
// import utils from './utils.js'
//公共请求部分配置
axios.defaults.baseURL = utils.api;
Vue.prototype.$utils = utils;
// axios.defaults.headers.common['logintoken'] = 'ccd0525ca39e78fa7b0e7a49032f64c1';

// axios.create({
//   headers: {'logintoken': "ccd0525ca39e78fa7b0e7a49032f64c1"}
// });

axios.interceptors.request.use(config => {
  var obj={};
  ajaxs.getUrlParam(obj)
  if(obj.logintoken){
    config.headers['logintoken'] =obj.logintoken
  }
  return config;
}, error => {
  console.log('request error', error)
  return Promise.reject('网络错误!')
})

const ajax = {
  post: function (url, data = {}) {
    return new Promise((resolve, reject) => {
      axios.post(url,
        qs.stringify(data))
        .then((response) => {
          resolve(response.data);
        })
        .catch((error) => {
          reject(error);
        });
    })
  },
  get: function (url, params = {}) {
    return new Promise((resolve, reject) => {
      axios.get(url, {
        params: params
      })
        .then((response) => {
          resolve(response.data);
        })
        .catch((error) => {
          reject(error);
        });
    })
  },
  put: function (url, data = {}) {
    return new Promise((resolve, reject) => {
      axios.put(url, qs.stringify(data))
        .then((response) => {
          resolve(response.data);
        })
        .catch((error) => {
          reject(error);
        });
    })
  },
  del: function (url, data = {}) {
    return new Promise((resolve, reject) => {
      axios.delete(url, qs.stringify(data))
        .then((response) => {
          resolve(response.data);
        })
        .catch((error) => {
          reject(error);
        });
    })
  }
};
// 请求拦截器
// axios.interceptors.request.use((config) => {
//   return config;
// }, function (error) {
//   Toast('请求失败！');
//   return Promise.reject(error);
// });
// 响应拦截器
axios.interceptors.response.use(function (response) {
  return response;
}, function (error) {
  console.log('错误：', error);
  Toast('请求失败！');
  return Promise.reject(error);
});

export default ajax;



