<template>
    <div id="content">
      <div class="notice_box">
        <p class="title_box">{{notice.title}}</p>
        <p class="tiem_box"><span>{{notice.created_at|formatDate('yyyy-MM-dd hh:mm') }}</span><span>作者：{{notice.teacher_name}}</span></p>
        <div class="content">
          <p v-html="notice.content"> {{notice.content}}</p>
        </div>
      </div>
    </div>
</template>

<script>
    export default {
      name: "NoticeDetail",
      props: {
        notice: {
          type: Object,
          default: "",
        }
      },
      computed:{

      },
    }
</script>

<style scoped>
  .notice_box{
    padding: 10px;
  }
  .top_bg{
    width: 100%;
    height: 1.9rem;
    object-fit: cover;
  }
  .title_box{
    font-size: .18rem;
    color: #333333;
  }
  .tiem_box{
    font-size: .12rem;
    color: #999999;
    margin: 10px 0;
  }
  .tiem_box span:last-child{
    float: right;
  }
  .content{
    font-size: .15rem;
    color: #666666;
    letter-spacing: 0;
    line-height: .3rem;
  }

</style>



