<template>
  <div id="custom">
    <NoticeDetail :notice="notice"></NoticeDetail>
    <div style="background-color: #fafafa;height: 10px"></div>
  </div>
</template>
<script>
  import  NoticeDetail from '../subViews/NoticeDetail'
  import  user from '../subViews/user'
  import api from '@/api/share'
  export default {
    name: "customizationDetails",
    components:{NoticeDetail,api,user},
    data () {
      return {
        notice:{},
      }
    },
    created(){
      this.GetnoticeDetail()
    },
    methods: {
      GetnoticeDetail() {
        api.noticeDetail({
        }).then(res => {
          console.log(res)
          if (res.status == 1) {
            this.notice = res.data.notice
          } else
            this.$toast(res.msg)
        })
      }
    }
  }
</script>

<style >
  #custom .content img{
    width: 100%;
    height: 1.9rem;
    object-fit: cover;
  }
  #custom .content video{
    width: 100%;
    height: 75%;
    margin: 10px 0;
  }
</style>



