<template>
  <div class="notice_box">
    <div class="user_list">
      <p v-if="show">评论</p>
      <ul class="clear">
        <li class="clear" v-for="item in datalist">
          <div class="img_box">
            <img :src="$utils.api+item.face"/>
          </div>
          <div class="right_box">
            <p class="name">{{item.nickname}}</p>
            <p class="timebox">{{item.add_time|formatDate('yyyy-MM-dd hh:mm')}}</p>
            <p class="right_content">{{item.contents}}</p>
            <p class="rightGive"><img src="../../assets/img/b19_icon1@3x.png" />{{item.type}}</p>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
    export default {
        name: "comment",
      props: {
        datalist: {
          type: Array,
        },
        show: {
          type:Boolean,
        },
      },
    }
</script>

<style scoped>
  .notice_box{
    padding: 10px;
  }
  .top_bg{
    width: 100%;
    height: 1.9rem;
    object-fit: cover;
  }
  .title_box{
    font-size: .18rem;
    color: #333333;
  }
  .tiem_box{
    font-size: .12rem;
    color: #999999;
    margin: 10px 0;
  }
  .tiem_box span:last-child{
    float: right;
  }
  .footer{
    position: fixed;
    width: 100%;
    bottom: 0;
    height: .49rem;
    line-height: .49rem;
    text-align: center;
    background-color: white;
  }
  .content{
    font-size: .15rem;
    color: #666666;
    letter-spacing: 0;
    line-height: .3rem;
    text-indent:2em
  }
  .footer p:last-child{
    float: right;
    background-color: #E80414;
    height: .4rem;
    line-height: .4rem;
    margin-bottom: .045rem;
    font-size: .14rem;
    padding: 0 20px;
    border-radius: 3px;
    color: white;
    margin-right: .1rem;
  }
  .footer input{
    background: #F1F1F1;
    float: left;
    height: .4rem;
    line-height: .4rem;
    margin-bottom: .05rem;
    padding: 0 10px;
    font-size: .14rem;
    width: 65%;
    margin-left: 10px;
  }
  .user_list{
    margin-bottom: .5rem;
  }
  .user_list>p{
    font-size: .14rem;
    color: #999999;
    padding: 10px 0;
    border-bottom: 1px solid  #EEEEEE;;
    margin-bottom: 10px;
  }
  .user_list ul li{
    margin-bottom: 5px;
  }
  .user_list ul li .right_box{
    float: right;
    width: 80%;
  }
  .user_list ul li .right_box .name{
    font-size: .13rem;
    color: #333333;
  }
  .user_list ul li .right_box .timebox{
    font-size: .12rem;
    color: #999999;
  }
  .user_list ul li .right_box .right_content{
    font-size: .14rem;
    color: #333333;
  }
  .user_list ul li .right_box .rightGive{
    font-size: .12rem;
    color: #999999;
  }
  .user_list ul li .right_box .rightGive img{
    width: .16rem;
    height: auto;
    border-radius:0;
    margin-right: 10px;
    position: relative;
    top:2px

  }
  .user_list ul li img{
    width: .5rem;
    height: .5rem;
    border-radius: 50%;
    object-fit: cover;
  }
  .img_box{
    text-align: center;
    float: left;
    width: 20%;
  }
  .user_list ul li p{
    margin: 5px 0;
  }
</style>



