// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import { Toast } from 'mint-ui';
import {
  formatDate,minutesFormat,money
} from '../src/assets/js/date';
Vue.config.productionTip = false
Vue.prototype.$toast = Toast;
import '../src/assets/js/axios.package';
import '../src/assets/js/rem';
import '../src/assets/scss/reset.scss';
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})

Vue.filter('formatDate', (value, fmt) => {
  return formatDate(value, fmt)
})
Vue.filter('minutesFormat', (value, fmt) => {
  return minutesFormat(value, fmt)
})
Vue.filter('money', (str) => {
  return money(str)
})

