<template>
  <div id="content">
    <div class="notice_box">
      <p class="title_box">{{teacher.name}}</p>
      <p class="tiem_box"><span>{{teacher.add_time|formatDate('yyyy-MM-dd hh:mm') }}</span><span>作者：{{teacher.teacher.name}}</span></p>
      <div class="content">
        <p v-html=teacher.contents> {{teacher.contents}}</p>
      </div>
    </div>
  </div>

</template>

<script>
  import  comment from '../subViews/comment'
  import api from '@/api/share'
  export default {
    name: 'mobileHotSpotDetails',
    components:{comment,api},
    data () {
      return {
        teacher:{},
        datalist:{}
      }
    },
    created(){
    this.Gethotdetails()
    // this.Getdatalist()
    },
    methods: {
      Gethotdetails() {
        api.hotdetails({
        }).then(res => {
          console.log(res)
          if (res.status == 1) {
            this.teacher=res.data

          } else
            this.$toast(res.msg)
        })
      },

    }
  }
</script>
<style >
  .footer{
    position: fixed;
    width: 100%;
    bottom: 0;
    height: .49rem;
    line-height: .49rem;
    text-align: center;
    background-color: white;
  }
  .footer p:last-child{
    float: right;
    background-color: #E80414;
    height: .4rem;
    line-height: .4rem;
    margin-bottom: .045rem;
    font-size: .14rem;
    padding: 0 20px;
    border-radius: 3px;
    color: white;
    margin-right: .1rem;
  }
  .footer input{
    background: #F1F1F1;
    float: left;
    height: .4rem;
    line-height: .4rem;
    margin-bottom: .05rem;
    padding: 0 10px;
    font-size: .14rem;
    width: 65%;
    margin-left: 10px;
  }
  .notice_box{
    padding: 10px;
  }
  .top_bg{
    width: 100%;
    height: 1.9rem;
    object-fit: cover;
  }
  .title_box{
    font-size: .18rem;
    color: #333333;
  }
  .tiem_box{
    font-size: .12rem;
    color: #999999;
    margin: 10px 0;
  }
  .tiem_box span:last-child{
    float: right;
  }
  .content{
    font-size: .15rem;
    color: #666666;
    letter-spacing: 0;
    line-height: .3rem;
    width: 100%;
  }
  #content .content img{
    width: 100%;
    height: 1.9rem;
    object-fit: cover;
  }
  #content .content video{
    width: 100%;
    height: 75%;
    margin: 10px 0;
  }
</style>



