function prefix(num, fill) {
  let len = ('' + num).length
  return Array(fill > len ? fill - len + 1 || 0 : 0).join(0) + num
}

export const formatDate = (stamp, fmt = 'YYYY-MM-dd hh:mm:ss') => {
  const date = new Date(stamp * 1000)

  if (/(y+)/.test(fmt)) {
    fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substring(4 - RegExp.$1.length))
  }
  let o = {
    'Y+': date.getFullYear(),
    'M+': date.getMonth() + 1,
    'd+': date.getDate(),
    'h+': date.getHours(),
    'm+': date.getMinutes(),
    's+': date.getSeconds()
  }
  for (let k in o) {
    if (new RegExp(`(${k})`).test(fmt)) {
      let str = o[k] + ''
      fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? str : prefix(str, 2))
    }
  }

  return fmt
}

export const money = (str) => { //给金钱结果加上人民币符号
  let returnMoney = str || 0 + '';
  return `¥${returnMoney}`
}

export const minutesFormat = (minutes, fmt = 'hh:mm') => {
  if (typeof minutes != 'number')
    return 0
  if (/(y+)/.test(fmt)) {
    fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substring(4 - RegExp.$1.length))
  }
  let o = {
    'h+': parseInt(minutes / 60),
    'm+': minutes % 60,
    's+': 0
  }
  for (let k in o) {
    if (new RegExp(`(${k})`).test(fmt)) {
      let str = o[k] + ''
      fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? str : prefix(str, 2))
    }
  }

  return fmt
}



