/**
 * 用户个人中心接口
 * */
import ajax from '../assets/js/axios.package';

export default {
  getUrlParam: function (obj) {
    var str = location.href; //取得整个地址栏
    var params = str.split("?")[1].split('&');
    for (var i = 0; i < params.length; i++) {
      var key = params[i].split("=")[0];
      var value = decodeURIComponent(params[i].split("=")[1]);
      obj[key] = value;
    }
    return obj
  },
  // 班级详情
  studyDetails() {
    const data={};
    this.getUrlParam(data)
    if(data){
      return ajax.get('/common/study/study_details', {
        id:data.id,
      });
    }
  },
  // 通知详情
  noticeDetail() {
    const data={};
    this.getUrlParam(data)
    if(data){
    return ajax.get('/common/study/notice_details', {
      id:data.id,
    });
    }
  },
  //热点详情
  hotdetails() {
    const data={};
    this.getUrlParam(data)
    if(data){
    return ajax.get('/common/study/hot_details', {
      id:data.id,
    });
    }
  },


  //热点详情
  sharehotdetails() {
    const data={};
    this.getUrlParam(data)
    if(data){
      return ajax.get('/common/study/hot_details', {
        id:data.id,
      });
    }
  },
//  评论列表
  datalist() {
    const data={};
    this.getUrlParam(data)
    if(data){
    return ajax.get('/discover/comment/datalist', {
      id:data.id,
      type:data.type,
      type_id:data.type_id,
    });
    }
  },

  //文章详情
  articleDetail() {
    const data={};
    this.getUrlParam(data)
    if(data) {
      return ajax.get('/discover/article/details', {
        id: data.id,
      });
    }
  },

  //专业课详情
    coursedetails() {
      const data={};
      this.getUrlParam(data)
      if(data) {
      return ajax.get('/discover/course/course_details', {
        id: data.id,
      });
    }
  }
}



