<template>
  <div>
    <div id="content">
      <div class="notice_box">
        <p class="title_box">{{data.name}}</p>
        <p class="tiem_box"><span>{{data.add_time|formatDate('yyyy-MM-dd hh:mm') }}</span><span>作者：{{data.author}}</span></p>
        <div class="content">
          <p v-html=data.content> {{data.content}}</p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import  NoticeDetail from '../subViews/NoticeDetail'
  import  user from '../subViews/user'
  import api from '@/api/share'
  export default {
    name: "InformDetails",
    components:{NoticeDetail,api,user},
    data () {
      return {
        data:{},
      }
    },
    created(){
      this.GetnoticeDetail()
    },
    methods: {
      GetnoticeDetail() {
        api.noticeDetail({
        }).then(res => {
          console.log(res)
          if (res.status == 1) {
            this.data = res.data.notice
          } else
            this.$toast(res.msg)
        })
      }
    }
  }
</script>

<style >

</style>



