<template>
    <div>
      <img :src=dataList.photo class="top_bg"/>
      <div class="content">
        <div class="title_box">
          <p>{{dataList.name}}</p>
          <p>开课时间：{{dataList.start |formatDate('YY-MM-dd')}}到{{dataList.end |formatDate('YY-MM-dd')}}</p>
        </div>
        <div class="message_list">
          <p><img src="../../assets/img/b5_icon2@3x.png" />价格：<span>{{dataList.price}}</span>元/人</p>
          <p><img src="../../assets/img/b5_icon3@3x.png" />班主任电话：{{dataList.teacher}}</p>
          <p><img src="../../assets/img/b5_icon4@3x.png" />报名人数：{{dataList.count}}人</p>
        </div>
      </div>
      <div style="background-color: #fafafa;height: 10px"></div>
      <div class="content">
        <p class="explain">班级说明</p>
        <p class="explain_content">{{content}}</p>
      </div>
    </div>

</template>

<script>
  import api from '@/api/share'
  export default {
  name: 'HelloWorld',
    components:{api},
  data () {
    return {
      dataList:{},
      content:""
    }
  },
    created(){
    this.GetDetails()
    },
  methods: {
   GetDetails() {
      api.studyDetails({
      }).then(res => {
        console.log(res)
        if (res.status == 1) {
          this.dataList = res.data
            this.content=(res.data.describe.replace(/<\/?.+?>/g,"")).replace(/ /g,"")
        } else
          this.$toast(res.msg)
      })
    }
  }
}
</script>
<style scoped >
  .top_bg{
    width: 100%;
    height: 1.9rem;
    object-fit: cover;
  }
  .content{
    padding: 10px;
  }
  .title_box p:first-child{
    font-size: .18rem;
    color: #333333;
  }
  .title_box p:last-child{
    font-size: .12rem;
    color: #999999;
    margin-top: 5px;
  }
  .message_list{
    padding: 3px 0;
    font-size: .14rem;
  }
  .message_list p{
    padding:2px 0;
  }
  .message_list p img{
    width: .23rem;
    margin-right:8px;
    position: relative;
    top:7px
  }
  .message_list p span{
    color: red;;
  }
  .explain{
    font-size: .13rem;
    color: #999999;
  }
  .explain_content{
    font-size: .13rem;
    color: #666;
    margin: 8px 0;
  }
  .explain_content img{
    width: 100%;
    height: 1.9rem;
    object-fit: cover;
  }
  .bottom{
    text-align: center;
    position: fixed;
    width: 100%;
    bottom: 0;
    height: .49rem;
    line-height: .49rem;
    background-color: white;
  }
  .consultation{
    background-color: white;
    font-size: .14rem;
    color: #666666;
  }
  .consultation img{
    width: .19rem;
    position: relative;
    top:4px;
    margin-right: 4px;
  }
  .bottom p{
    width: 33.3333%;
    float: left;
  }
  .questionnaire{
    background: #E1E1E1;
    font-size: .14rem;
    color: #666666;
  }
  .bottom p:last-child{
    background: #E80414;
    font-size: .14rem;
    color: white;
  }
</style>



